  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  flex-direction: column;
  background-color: #2fa8cc;
  font-family: "Roboto", sans-serif;
  justify-content: center;
  padding: 5px;
  font-size: 12px;
}

img {
  max-width: 5rem;
  max-height: 7rem;
}

h1, h2, h3, li, input {
  color: #002d67;
}

h1 {
  font-size: 1.2rem;
}

h2 {
  font-size: 0.7rem;
}

h3 {
  font-size: 0.5rem;
}

li, input, a, .copyRights {
  border-radius: 0.15rem;
  font-size: 0.7rem;
}

header {
  padding: 0.2rem;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: center;
}

nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  gap: 5rem;
  white-space: nowrap;
}
nav img {
  flex: 0.8;
  align-self: flex-start;
}
nav h2 {
  flex: 0.4;
}

#pokemonList {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-auto-rows: 1fr;
  align-items: center;
  padding: 0.2rem;
  gap: 0.7rem;
}
#pokemonList img {
  max-width: 6rem;
  max-height: 4rem;
}

.pokemonItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.4rem;
  background: #72bbce;
  border-radius: 3px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);
}

.likeDiv, .commentDiv {
  display: flex;
  flex-direction: row;
  align-self: stretch;
  align-items: stretch;
  justify-content: space-between;
  padding: 0.2rem;
}
.likeDiv div, .likeDiv img, .commentDiv div, .commentDiv img {
  align-self: flex-end;
}
.likeDiv img, .commentDiv img {
  max-width: 1rem !important;
  max-height: 1rem !important;
}

button {
  transition: all 0.5s;
  border-radius: 4px;
  font-size: 0.6rem;
  font-weight: 600;
  color: #002d67;
  background-color: #2fa8cc !important;
  border: 1px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);
  padding: 4px;
  cursor: pointer;
  text-align: center;
  align-self: flex-start;
}
button:active {
  transform: scale(0.98);
}
button:focus {
  outline: 0;
}
button:active {
  transform: scale(0.98);
}
button:focus {
  outline: 0;
}

footer {
  position: relative;
  margin-top: 2rem;
  padding-bottom: 0.3rem;
  top: auto;
  bottom: 0.1rem;
